.experience-sec {
    background-color: #01153E;
    color: #ccd6f6;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    height: 100vh;
    padding: 60px 20px 20px;
}

.experience-sec h2 {
    font-size: 42px;
    margin-bottom: 16px;
    color: #64ffda;
  }

  .download-button {
    display: inline-block;
    margin-top: 20px;
    background-color: transparent;
    color: #64ffda;
    border: 1px solid #64ffda;
    padding: 10px 20px;
    font-size: 15px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .download-button:hover {
    background-color: rgba(100, 255, 218, 0.1);
  }

  .timeline {
    position: relative;
    max-width: 1200px;
    margin: 100px auto;
  }
  .container {
    padding: 10px 50px;
    position: relative;
    width: 50%;
    background: rgba(0,0,0,0.3);
  }

  .text-box {
    padding: 20px 30px;
    background: #fff;
    position: relative;
    border-radius: 6px;
    font-size: 15px; 
  }

  .left-container {
    left: 0;
  }

  .right-container {
    left: 50%;
  }

  .container img {
    position: absolute;
    width: 40px;
    border-radius: 50%;
    right: -20px;
    top: 32px;
    z-index: 10;
  }
  .right-container img {
    left: -20px;
  }

  .timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    height: 100%;
    background: #fff;
    top: 0;
    left: 50%;
    margin-left: -3px;
    z-index: -1; 
  }